export default {
    layout : "main-content",
    data(){
        return {
            wizard : {
                show : true,
                current_step : 1,
                navigable : true,
            }
        }
    },
    methods : {
        goto_step(selected_step) {
            if (this.wizard.navigable && (this.wizard.current_step > selected_step)) {
                this.wizard.current_step = selected_step;
            }
        },
    }
}
